.container {
  min-width: 320px;
  padding-left: 350px;
  padding-right: 350px;

  @include vp-1919 {
    padding-left: percentage(calc(350 / 1920));
    padding-right: percentage(calc(350 / 1920));
  }

  @include vp-1023 {
    padding-left: percentage(calc(50 / 770));
    padding-right: percentage(calc(50 / 770));
  }

  @include vp-767 {
    padding-left: percentage(calc(25 / 320));
    padding-right: percentage(calc(25 / 320));
  }

  @media (max-width: 320px) {
    padding-left: 25px;
    padding-right: 25px;
  }
}
